.enter-container{
  padding: 15%;
  transition: all 2s;
  height: 100%;
}

.enter-container div{
  display: block;
  opacity: 0;
  position: absolute;
  left: 50%;
  top: calc(50% + 2rem);
  transform: translate(-50%, -70px);
}

.enter-container div .enterButton{
  width: calc(100px + 5vw)
}

.fade-appear{
  opacity: 0;
}

.fade-appear.fade-appear-active{
  opacity: 1;
  transition: opacity 500ms linear;
}

.fade-exit{
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0;
  transition: opacity 500ms linear;
}

.fade-exit-done{
  opacity: 0
}

.fade-enter{
  opacity: 0;
}

.fade-enter.fade-enter-active{
  opacity: 1;
  transition: opacity 500ms linear;
}
