#event{
  margin-top: 8%;
}

#event button{
  margin-top: 8%;
  width: 50%;
  min-width: 150px;
  max-width: 400px;
  height: 50px;
  border-radius: 10px;
  background-color: rgba(200,200,200,0.7)
}

#event button:hover{
  background-color: rgba(230,230,230,1);
  transition: 0.5s all linear;
}
