#risk-count .increasing{
  color: green;
}
#risk-count .decreasing{
  color: red;
}

#risk-count .data{
  min-width: 90px;
}

#risk-count table th,td{
  padding: 0;
  margin: 0;
  line-height: 100%;
}


#risk-count td:nth-child(3){
  display: inline-block;
  min-width: 20px;
  width: 2rem;
}
