@-webkit-keyframes shiver {
  0%{
    -webkit-transform: translate(0px,0px);
            transform: translate(0px,0px);
  }
  10%{
    -webkit-transform: translate(1px,0px);
            transform: translate(1px,0px);
  }
  30%{
    -webkit-transform: translate(-1px,0px);
            transform: translate(-1px,0px);
  }
  50%{
    -webkit-transform: translate(1px,0px);
            transform: translate(1px,0px);
  }
  70%{
    -webkit-transform: translate(-1px,0px);
            transform: translate(-1px,0px);
  }
  100%{
    -webkit-transform: translate(0px,0px);
            transform: translate(0px,0px);
  }
}

@keyframes shiver {
  0%{
    -webkit-transform: translate(0px,0px);
            transform: translate(0px,0px);
  }
  10%{
    -webkit-transform: translate(1px,0px);
            transform: translate(1px,0px);
  }
  30%{
    -webkit-transform: translate(-1px,0px);
            transform: translate(-1px,0px);
  }
  50%{
    -webkit-transform: translate(1px,0px);
            transform: translate(1px,0px);
  }
  70%{
    -webkit-transform: translate(-1px,0px);
            transform: translate(-1px,0px);
  }
  100%{
    -webkit-transform: translate(0px,0px);
            transform: translate(0px,0px);
  }
}

@-webkit-keyframes glow {
  0%{
    text-shadow: 0px 0px 2px rgba(255, 228, 51, 1);
  }
  50%{
    text-shadow: 0px 0px 6px rgba(255, 228, 51, 1),
                  0px 0px 10px rgba(255, 228, 51, 1);
  }
  100%{
    text-shadow: 0px 0px 2px rgba(255, 228, 51, 1);
  }
}

@keyframes glow {
  0%{
    text-shadow: 0px 0px 2px rgba(255, 228, 51, 1);
  }
  50%{
    text-shadow: 0px 0px 6px rgba(255, 228, 51, 1),
                  0px 0px 10px rgba(255, 228, 51, 1);
  }
  100%{
    text-shadow: 0px 0px 2px rgba(255, 228, 51, 1);
  }
}

@-webkit-keyframes heat {
  0%{
    opacity: 0.5
  }
  70%{
    opacity: 1
  }
  100%{
    opacity: 0.5
  }
}

@keyframes heat {
  0%{
    opacity: 0.5
  }
  70%{
    opacity: 1
  }
  100%{
    opacity: 0.5
  }
}

